/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { useEffect } from 'react';
import { Container, Modal } from 'reactstrap';
import { CLOSE_ICON } from '../../../assets/images';
import './modal.css';

function CustomModal({
  isOpen,
  toggle,
  scrollable,
  title,
  children,
  className,
  SubHeaderComponent,
}) {
  useEffect(() => {
    if (isOpen) document.body.style.overflow = 'hidden';
    else document.body.style.overflow = 'visible';
  }, [isOpen]);
  return (
    <Modal
      isOpen={isOpen}
      scrollable={scrollable || true}
      className={`modal-dialog-centered custom-modal ${className}`}
    >
      <Container className="px-3">
        <div className="modal-header h4 pb-0 pt-2 border-0">
          <span className="modal-title">{title}</span>
          <button type="button" className="text-button" onClick={toggle}>
            <img src={CLOSE_ICON} alt="" />
          </button>
          {/* <i className="fa fa-times cursor-pointer" onClick={toggle} /> */}
        </div>
      </Container>
      <hr />
      {SubHeaderComponent && <SubHeaderComponent />}
      <Container className="modal-container">
        <div className="modal-body px-4 py-2">
          <div className="p-2">{children}</div>
        </div>
      </Container>
    </Modal>
  );
}

export default CustomModal;
