import React, { useEffect, useState } from 'react';

import './NotificationAlert.css';
import { useSelector, useDispatch } from 'react-redux';
import { setAlertData } from '../../../store/actions';

const NotificationAlert = props => {
  const { type, message, delay } = props;
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const { alertData } = useSelector(state => state.dashboard);
  console.log(isOpen);
  useEffect(() => {
    setIsOpen(true);
    setTimeout(() => {
      dispatch(setAlertData({ isOpen: false, message: '', type: '' }));
      setIsOpen(false);
    }, delay);
  }, [alertData]);

  return (
    <div className={`notification ${type} ${isOpen ? 'show' : 'hide'}`}>
      {/* <i 
            className="close fa-solid fa-xmark" 
            role="button"
            aria-label="Close"
            tabIndex={0}
        /> */}
      {message}
    </div>
  );
};

export default NotificationAlert;
