/* eslint-disable no-unused-vars */
/* eslint-disable radix */
/* eslint-disable import/prefer-default-export */
import { call, put } from 'redux-saga/effects';
import {
  getFeatureSuccess,
  getFeatureFail,
  createGroupSuccess,
  getAllUsersFail,
  getAllUsersSuccess,
  createUserSuccess,
  getUserGroupSuccess,
  getUserGroupFail,
  createGroupFail,
  createUserFail,
  loginSuccess,
  getUserStart,
  getUserSuccess,
} from '../../actions';
import { errorHandler } from '../../../utils';

function* getFeatureStartsaga() {
  yield errorHandler({
    endpoint: `/v1/webapp/getFeatures`,
    successHandler: yield function* (response) {
      yield put(
        getFeatureSuccess({
          response: response.response,
        }),
      );
    },
    failHandler: yield function* (response) {
      yield put(getFeatureFail(response));
    },

    failHandlerType: 'CUSTOM',
    apiType: 'get',
  });
}

function* getUserGroupsaga(action) {
  yield errorHandler({
    endpoint: `/v1/webapp/getUserGroup`,
    successHandler: yield function* (response) {
      yield put(
        getUserGroupSuccess({
          response: response.response,
        }),
      );
    },
    failHandler: yield function* (response) {
      yield put(getUserGroupFail(response));
    },
    payload: {
      pageNumber: action.payload.currPage,
      perPage: action.payload.perPage,
      filters: [
        {
          name: 'status',
          value: 'Active',
        },
      ],
      sortKey: {
        attibName: 'updatedAt',
        sortType: 'desc',
      },
    },
    failHandlerType: 'CUSTOM',
    apiType: 'post',
  });
}

function* createGroupsaga(action) {
  const { data, onCreateGroup } = action.payload;
  yield errorHandler({
    endpoint: `/v1/webapp/saveUserGroup`,
    successHandler: yield function* (response) {
      if (response.responseCode === '500') {
        yield put(createGroupFail(response));
        onCreateGroup('fail');
      } else {
        yield put(
          createGroupSuccess({
            response: response.response,
          }),
        );
        onCreateGroup('success');
      }
    },
    failHandlerType: 'CUSTOM',
    payload: data,
    apiType: 'post',
  });
}

function* createUsersaga(action) {
  const { data, handleSuccess } = action.payload;
  yield errorHandler({
    endpoint: `/v1/webapp/saveUser`,
    successHandler: yield function* (response) {
      if (parseInt(response.responseCode) === 500) {
        handleSuccess('error');
        yield put(createUserFail());
      } else {
        yield put(
          createUserSuccess({
            response: response.response,
          }),
        );
        handleSuccess('success');
      }
    },
    failHandlerType: 'CUSTOM',
    payload: data,
    apiType: 'post',
  });
}

function* getAllUsersaga(action) {
  yield errorHandler({
    endpoint: `/v1/webapp/getUser`,
    successHandler: yield function* (response) {
      yield put(
        getAllUsersSuccess({
          response: response.response,
        }),
      );
    },
    failHandler: yield function* (response) {
      yield put(getAllUsersFail(response));
    },
    payload: {
      pageNumber: action.payload.currPage,
      perPage: action.payload.perPage,
      filters: [
        {
          name: 'status',
          value: 'Active',
        },
      ],
      sortKey: {
        attibName: 'updatedAt',
        sortType: 'desc',
      },
    },
    failHandlerType: 'CUSTOM',
    apiType: 'post',
  });
}
function* switchUserSaga(action) {
  const { data, handleSuccess } = action.payload;
  yield errorHandler({
    endpoint: `/v1/webapp/changeLoggedInUserGroup`,
    successHandler: yield function* (response) {
      if (parseInt(response.responseCode) === 500) {
        handleSuccess('error');
      } else {
        console.log(response);
        yield call([localStorage, 'setItem'], 'authToken', response.accessToken);
        // yield put(getUserSuccess(response));
        handleSuccess('success', response);
      }
    },
    failHandlerType: 'CUSTOM',
    payload: data,
    apiType: 'post',
  });
}

export {
  getFeatureStartsaga,
  createGroupsaga,
  getUserGroupsaga,
  getAllUsersaga,
  createUsersaga,
  switchUserSaga,
};
