import * as actionLabels from '../../actionLabels';

export const initialState = {
  isLogin: false,
  isLoading: false,
  userData: null,
  authToken: '',
  errorMsg: '',
  fcmToken: '',
  featureList: [],
  AllUsers: [],
  AllGroups: [],
  ModiFygroup: false,
  isCreating: false,
  modifyrow: {},
  createGroupsuccess: '',
  createUsersuccess: '',
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case actionLabels.GET_FEATURE_LIST_START:
      return { ...state, isLoading: true };
    case actionLabels.GET_FEATURE_LIST_SUCCESS: {
      return {
        ...state,
        featureList: payload.response,
        errorMsg: '',
        isLoading: false,
      };
    }
    case actionLabels.GET_FEATURE_LIST_FAIL: {
      return { ...state, isLoading: false, errorMsg: payload };
    }
    case actionLabels.CREATE_GROUP_START: {
      return { ...state, isLoading: true, isCreating: true };
    }
    case actionLabels.CREATE_GROUP_SUCCESS: {
      return { ...state, isLoading: false, createGroupsuccess: 'success', isCreating: false };
    }
    case actionLabels.CREATE_GROUP_FAIL: {
      return { ...state, isLoading: false, createGroupsuccess: 'Fail', isCreating: false };
    }
    case actionLabels.CREATE_USER_START: {
      return { ...state, isCreating: true };
    }
    case actionLabels.CREATE_USER_SUCCESS: {
      return { ...state, createUsersuccess: 'success', isCreating: false };
    }
    case actionLabels.CREATE_USER_FAIL: {
      return { ...state, createUsersuccess: 'fail', isCreating: false };
    }
    case actionLabels.GETALL_USERS_START: {
      return { ...state, isLoading: true };
    }
    case actionLabels.GETALL_USERS_SUCCESS: {
      return { ...state, isLoading: false, AllUsers: payload };
    }
    case actionLabels.GETALL_USERS_FAIL: {
      return { ...state, isLoading: false, errorMsg: payload };
    }
    case actionLabels.GET_USER_GROUP_START: {
      return { ...state, isLoading: true };
    }
    case actionLabels.GET_USER_GROUP_SUCCESS: {
      return { ...state, isLoading: false, AllGroups: payload };
    }
    case actionLabels.GET_USER_GROUP_FAIL: {
      return { ...state, isLoading: false, errorMsg: payload };
    }
    case actionLabels.SET_MODIFY_GROUP: {
      return {
        ...state,
        isLoading: false,
        modifyrow: payload.modifyrow,
        ModiFygroup: payload.ModiFygroup,
      };
    }
    case actionLabels.REMOVE_MODIFY_GROUP: {
      return {
        ...state,
        isLoading: false,
        modifyrow: {},
        ModiFygroup: false,
      };
    }
    default:
      return state;
  }
};
