import * as actionLabels from '../../actionLabels';

export const initialState = {
  isLoading: false,
  shipmentData: null,
  errorMsg: '',
  successMsg: '',
  uploadData: null,
  alertData: {
    isOpen: false,
    type: 'success',
    message: '',
  },
  filters: null,
  currPage: 0,
  perPage: 10,
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case actionLabels.ADD_SHIPMENT_DATA_START:
      return { ...state, isLoading: true };
    case actionLabels.ADD_SHIPMENT_DATA_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        successMsg: payload,
        errorMsg: '',
        alertData: {
          isOpen: true,
          type: 'success',
          message: payload,
        },
      };
    }
    case actionLabels.ADD_SHIPMENT_DATA_FAIL: {
      return {
        ...state,
        isLoading: false,
        errorMsg: payload,
        successMsg: '',
        alertData: {
          isOpen: true,
          type: 'error',
          message: payload,
        },
      };
    }
    case actionLabels.GET_SHIPMENT_DATA_START:
      return { ...state, isLoading: true, successMsg: '' };
    case actionLabels.GET_SHIPMENT_DATA_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        shipmentData: payload,
        errorMsg: '',
      };
    }
    case actionLabels.GET_SHIPMENT_DATA_FAIL: {
      return { ...state, isLoading: false, errorMsg: payload, successMsg: '', shipmentData: null };
    }
    case actionLabels.DELETE_SHIPMENT_DATA_START:
      return { ...state, isLoading: true };
    case actionLabels.DELETE_SHIPMENT_DATA_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        errorMsg: '',
        alertData: {
          isOpen: true,
          type: 'success',
          message: 'Record Deleted',
        },
      };
    }
    case actionLabels.DELETE_SHIPMENT_DATA_FAIL: {
      return {
        ...state,
        isLoading: false,
        errorMsg: payload,
        successMsg: '',
        alertData: {
          isOpen: true,
          type: 'error',
          message: payload,
        },
      };
    }
    case actionLabels.UPLOAD_DATA_START:
      return { ...state, isLoading: true };
    case actionLabels.UPLOAD_DATA_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        errorMsg: '',
        alertData: {
          isOpen: true,
          type: 'success',
          message: 'File Uploaded Successfully',
        },
      };
    }
    case actionLabels.UPLOAD_DATA_FAIL: {
      return {
        ...state,
        isLoading: false,
        errorMsg: payload,
        successMsg: '',
        alertData: {
          isOpen: true,
          type: 'error',
          message: 'Upload Failed',
        },
      };
    }
    case actionLabels.GET_UPLOAD_DATA_START:
      return { ...state, isLoading: true };
    case actionLabels.GET_UPLOAD_DATA_SUCCESS: {
      return {
        ...state,
        uploadData: payload,
        isLoading: false,
        errorMsg: '',
      };
    }
    case actionLabels.GET_UPLOAD_DATA_FAIL: {
      return { ...state, isLoading: false, errorMsg: payload, successMsg: '', uploadData: null };
    }
    case actionLabels.GET_HISTORY_DATA_START:
      return { ...state, isLoading: true };
    case actionLabels.GET_HISTORY_DATA_SUCCESS: {
      return {
        ...state,
        historyDataObj: payload,
        isLoading: false,
        errorMsg: '',
      };
    }
    case actionLabels.GET_HISTORY_DATA_FAIL: {
      return { ...state, isLoading: false, errorMsg: payload, successMsg: '' };
    }
    case actionLabels.SET_ALERT_DATA: {
      return {
        ...state,
        alertData: {
          isOpen: payload.isOpen,
          type: payload.type,
          message: payload.message,
        },
      };
    }
    case actionLabels.GET_FILTER_DATA_START:
      return { ...state, isLoading: true };
    case actionLabels.GET_FILTER_DATA_SUCCESS: {
      return {
        ...state,
        filters: payload[0],
        isLoading: false,
        errorMsg: '',
      };
    }
    case actionLabels.GET_FILTER_DATA_FAIL: {
      return { ...state, isLoading: false, errorMsg: payload, successMsg: '' };
    }
    case actionLabels.SET_CURRENT_PAGE: {
      return {
        ...state,
        currPage: payload,
      };
    }
    case actionLabels.SET_PER_PAGE: {
      return {
        ...state,
        perPage: payload,
      };
    }
    default:
      return state;
  }
};
