import React from 'react';

export const guestRoutes = [
  {
    path: '/login',
    name: 'LogIn',
    exact: true,
    component: React.lazy(() => import('../../views/auth/Login/Login')),
  },
];

export const userRoutes = [
  {
    path: '/dashboard',
    name: 'Dashboard',
    exact: true,
    component: React.lazy(() => import('../../views/user/Dashboard/Dashboard')),
  },
  {
    path: '/upload-list',
    name: 'UploadList',
    exact: true,
    component: React.lazy(() => import('../../views/user/UploadList/UploadList')),
  },
  {
    path: '/data-history/:id',
    name: 'DataHistory',
    exact: true,
    component: React.lazy(() => import('../../views/user/DataHistory/DataHistory')),
  },
  {
    path: '/access-management/',
    name: 'AccessManagement',
    exact: true,
    component: React.lazy(() => import('../../views/user/AccessManagement/AccessManagement')),
  },
  {
    redirectRoute: true,
    path: '/dashboard',
    name: 'Redirect',
  },
];
