import { all, takeLatest } from 'redux-saga/effects';
import * as actionLabels from '../actionLabels';
import {
  createGroupsaga,
  createUsersaga,
  getAllUsersaga,
  switchUserSaga,
  getUserGroupsaga,
  getFeatureStartsaga,
} from './accessmanagement';
import { loginSaga, authenticationValidatorSaga, getUserSaga } from './auth/auth';
import {
  addShipmentData,
  getShipmentData,
  deleteShipmentData,
  uploadFileData,
  getUploadData,
  getHistoryData,
  getFilterData,
} from './dashboard/dashboard';

// eslint-disable-next-line import/prefer-default-export
export function* watchAuthentication() {
  yield all([
    takeLatest(actionLabels.GETUSER_START, getUserSaga),
    takeLatest(actionLabels.LOGIN_SAGA, loginSaga),
    takeLatest(actionLabels.AUTHENTICATION_VALIDATOR, authenticationValidatorSaga),
  ]);
}

export function* watchAccessManagement() {
  yield all([takeLatest(actionLabels.GET_FEATURE_LIST_START, getFeatureStartsaga)]);
  yield all([takeLatest(actionLabels.CREATE_GROUP_START, createGroupsaga)]);
  yield all([takeLatest(actionLabels.GETALL_USERS_START, getAllUsersaga)]);
  yield all([takeLatest(actionLabels.CREATE_USER_START, createUsersaga)]);
  yield all([takeLatest(actionLabels.SWITCH_GROUP_START, switchUserSaga)]);
  yield all([takeLatest(actionLabels.GET_USER_GROUP_START, getUserGroupsaga)]);
}

export function* watchDashboard() {
  yield all([takeLatest(actionLabels.ADD_SHIPMENT_DATA, addShipmentData)]);
  yield all([takeLatest(actionLabels.GET_SHIPMENT_DATA, getShipmentData)]);
  yield all([takeLatest(actionLabels.DELETE_SHIPMENT_DATA, deleteShipmentData)]);
  yield all([takeLatest(actionLabels.UPLOAD_DATA, uploadFileData)]);
  yield all([takeLatest(actionLabels.GET_UPLOAD_DATA, getUploadData)]);
  yield all([takeLatest(actionLabels.GET_HISTORY_DATA, getHistoryData)]);
  yield all([takeLatest(actionLabels.GET_FILTER_DATA, getFilterData)]);
}
