import { put } from 'redux-saga/effects';
import { errorHandler } from '../../../utils';
import {
  addShipmentDataFail,
  addShipmentDataStart,
  addShipmentDataSuccess,
  getShipmentDataFail,
  getShipmentDataStart,
  getShipmentDataSuccess,
  deleteShipmentDataFail,
  deleteShipmentDataSuccess,
  deleteShipmentDataStart,
  uploadFileDataStart,
  uploadFileDataSuccess,
  uploadFileDataFail,
  getUploadDataStart,
  getUploadDataFail,
  getUploadDataSuccess,
  getHistoryDataStart,
  getHistoryDataFail,
  getHistoryDataSuccess,
  getFilterDataStart,
  getFilterDataSuccess,
  getFilterDataFail,
  setCurrPage,
  setPerPage,
} from '../../actions';

export function* addShipmentData(action) {
  const { values, toggle } = action.payload;
  yield put(addShipmentDataStart());

  yield errorHandler({
    endpoint: '/v1/webapp/saveShipmentData',
    successHandler: yield function* (response) {
      yield put(addShipmentDataSuccess(response.message));
      yield toggle();
      yield put(setCurrPage(0));
      yield put(setPerPage(10));
    },
    failHandler: yield function* (response) {
      yield put(addShipmentDataFail(response));
    },
    failHandlerType: 'CUSTOM',
    payload: values,
    apiType: 'post',
  });
}

export function* getShipmentData(action) {
  // const { values } = action.payload;
  yield put(getShipmentDataStart());

  yield errorHandler({
    endpoint: '/v1/webapp/searchShipmentData',
    successHandler: yield function* (response) {
      yield put(getShipmentDataSuccess(response.response[0]));
    },
    failHandler: yield function* (response) {
      yield put(getShipmentDataFail(response));
    },
    failHandlerType: 'CUSTOM',
    payload: action.payload,
    apiType: 'post',
  });
}

export function* deleteShipmentData(action) {
  const { values, getData, setToggleDeleteData } = action.payload;
  yield put(deleteShipmentDataStart());
  yield errorHandler({
    endpoint: '/v1/webapp/deleteShipmentData',
    successHandler: yield function* (response) {
      yield put(deleteShipmentDataSuccess(response));
      yield setToggleDeleteData(false);
      yield getData(0);
      yield put(setCurrPage(0));
      yield put(setPerPage(10));
    },
    failHandler: yield function* (response) {
      yield put(deleteShipmentDataFail(response));
    },
    failHandlerType: 'CUSTOM',
    payload: values,
    apiType: 'post',
  });
}

export function* uploadFileData(action) {
  const { file, setToggleAddData, getData } = action.payload;
  yield put(uploadFileDataStart());
  yield errorHandler({
    endpoint: '/v1/webapp/upload',
    successHandler: yield function* (response) {
      yield put(uploadFileDataSuccess(response));
      yield setToggleAddData(false);
      yield getData(0);
    },
    failHandler: yield function* (response) {
      yield put(uploadFileDataFail(response));
      yield getData(0);
      yield setToggleAddData(false);
    },
    failHandlerType: 'CUSTOM',
    payload: file,
    apiType: 'post',
  });
}

export function* getUploadData(action) {
  yield put(getUploadDataStart());

  yield errorHandler({
    endpoint: '/v1/webapp/searchExcelUploadInfo',
    successHandler: yield function* (response) {
      yield put(getUploadDataSuccess(response.response[0]));
    },
    failHandler: yield function* (response) {
      yield put(getUploadDataFail(response));
    },
    failHandlerType: 'CUSTOM',
    payload: action.payload,
    apiType: 'post',
  });
}

export function* getHistoryData(action) {
  yield put(getHistoryDataStart());

  yield errorHandler({
    endpoint: `/v1/webapp/getShipmentDataVersions/${action.payload}`,
    successHandler: yield function* (response) {
      yield put(getHistoryDataSuccess(response.response));
    },
    failHandler: yield function* (response) {
      yield put(getHistoryDataFail(response));
    },
    failHandlerType: 'CUSTOM',
    apiType: 'get',
  });
}

export function* getFilterData() {
  yield put(getFilterDataStart());

  yield errorHandler({
    endpoint: `/v1/webapp/getFilterData`,
    successHandler: yield function* (response) {
      yield put(getFilterDataSuccess(response.response));
    },
    failHandler: yield function* (response) {
      yield put(getFilterDataFail(response));
    },
    failHandlerType: 'CUSTOM',
    apiType: 'get',
  });
}
